// Reference to the gallery container
const galleryContainer = document.querySelector('.gallery');

// Function to create gallery markup
const createMarkup = images.map(({ preview, original, description }) => `
  <li class="gallery-item">
    <a class="gallery-link" href="${original}">
      <img 
        class="gallery-image" 
        src="${preview}" 
        alt="${description}" 
        width="360"
      />
    </a>
  </li>
`).join('');

// Insert markup into the gallery container
galleryContainer.innerHTML = createMarkup;

// Initialize SimpleLightbox after rendering the gallery
const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',     // Use 'alt' attribute for captions
  captionDelay: 250,       // Delay showing captions by 250ms
});
